}
a .icon-wailian,
  a .wailian {
    width: 0;
    overflow: hidden;
}
a:hover .icon-wailian,
  a:hover .wailian {
    width: 20px;
}
a[title="站长统计"] {
    display: none;
}
p {
  text-align: justify;
  margin: 5px 0;
  line-height: 1.6;
}
.iconfont {
  margin-right: 10px;
  font-size: 110%;
}
.iconfont.icon-star-1 {
    margin-right: 0;
}
.iconfont.icon-star-1:before {
      content: "\e618";
}
.iconfont.icon-star-2 {
    margin-right: 0;
}
.iconfont.icon-star-2:before {
      content: "\e618  \e618";
}
.iconfont.icon-star-3 {
    margin-right: 0;
}
.iconfont.icon-star-3:before {
      content: "\e618  \e618  \e618";
}
.iconfont.icon-star-4 {
    margin-right: 0;
}
.iconfont.icon-star-4:before {
      content: "\e618  \e618  \e618  \e618";
}
.iconfont.icon-star-5 {
    margin-right: 0;
}
.iconfont.icon-star-5:before {
      content: "\e618  \e618  \e618  \e618  \e618";
}
.iconsvg {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  color: inherit;
  margin-right: 10px;
}
.ib {
  display: inline-block;
  vertical-align: middle;
}
.f120 {
  font-size: 120%;
}
.f110 {
  font-size: 110%;
}
.f90 {
  font-size: 90%;
}
.f80 {
  font-size: 80%;
}
.gray {
  color: #666;
}
.nowrap {
  white-space: nowrap;
}
.mt20 {
  margin-top: 20px;
}
.hide {
  overflow: hidden;
}
.normal {
  white-space: initial !important;
}
.maskLayer {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(255, 255, 255, 0.7);
  z-index: 10;
  display: none;
}
aside.aside-left {
  width: 350px;
  position: absolute;
  top: 0;
  left: 0;
  background: #29B6F6;
  color: #fff;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  z-index: 9;
}
aside.aside-left .banner-bg {
    height: 200px;
    background: url(../images/banner-bg.jpg) no-repeat center center;
    background-size: 200%;
    position: relative;
}
aside.aside-left .banner-bg .myInfo {
      width: 100%;
      height: 100px;
      position: absolute;
      bottom: -50px;
      padding: 0 20px;
}
aside.aside-left .banner-bg .myInfo .myHeader {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: #fff;
        overflow: hidden;
        text-align: center;
        line-height: 100px;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.14);
        border: 2px solid #fff;
}
aside.aside-left .banner-bg .myInfo .myHeader img {
          width: 100%;
          height: 100%;
          vertical-align: middle;
}
aside.aside-left .banner-bg .myInfo .jobName {
        font-size: 26px;
        padding: 10px 0;
}
aside.aside-left .banner-bg .myInfo .myName {
        font-size: 21px;
        padding: 10px 0;
}
aside.aside-left .baseInfo {
    margin-top: 70px;
}
aside.aside-left .baseInfo h1 {
      font-weight: normal;
}
aside.aside-left .baseInfo li {
      padding: 10px 20px;
}
aside.aside-right {
  width: 100%;
  height: 100%;
}
aside.aside-right section {
    width: calc((100% - 350px)/2 - 10px);
    position: absolute;
    background: #fff;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
    padding: 20px 20px 20px 30px;
    overflow: hidden;
}
aside.aside-right section.section-left {
      top: 0;
      right: calc((100% - 350px)/2);
}
aside.aside-right section.section-right {
      top: 0;
      right: 0;
}
aside.aside-right section .time-list {
      border-left: 2px solid #29B6F6;
}
aside.aside-right section .time-list > .list {
        padding-left: 20px;
        margin-bottom: 40px;
}
aside.aside-right section .time-list > .list:last-child {
          margin-bottom: 0;
}
aside.aside-right section .time-list > .list h1 {
          color: #29B6F6;
          font-size: 20px;
          font-weight: 600;
          position: relative;
          margin-bottom: 30px;
}
aside.aside-right section .time-list > .list h1:before {
            content: '';
            position: absolute;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background: #29B6F6;
            left: -31px;
            top: 0;
}
aside.aside-right section .time-list > .list .min-img img {
          width: 100%;
}
aside.aside-right section .time-list > .list dl {
          margin-top: 15px;
}
aside.aside-right section .time-list > .list dl dt {
            position: relative;
            font-weight: bold;
            margin-bottom: 10px;
}
aside.aside-right section .time-list > .list dl dt:before {
              content: '';
              position: absolute;
              width: 12px;
              height: 12px;
              border: 1px solid #29B6F6;
              background: #fff;
              border-radius: 50%;
              left: -28px;
              top: 0;
}
