.fixedTo {
  position: fixed !important;
  top: auto !important;
  top: 0 !important;
}
.fixedBottom {
  position: fixed !important;
  top: auto !important;
  bottom: 0 !important;
}
.fixed {
  position: fixed;
}
.workTime {
  font-size: 80%;
  color: #666;
}
.content {
  padding-left: 350px;
  position: relative;
}
@-webkit-keyframes slideIn {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 200px, 0);
    transform: translate3d(0, 200px, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}
}
@keyframes slideIn {
from {
    opacity: 0;
    -webkit-transform: translate3d(0, 200px, 0);
    transform: translate3d(0, 200px, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}
}
.slideIn {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: slideIn;
  animation-name: slideIn;
}
.delay100 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.delay200 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.delay2000 {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}
@media only screen and (max-width: 1100px) {
aside.aside-right section {
    width: 99%;
    position: relative;
}
aside.aside-right section.section-left {
      left: 1%;
      margin-bottom: 10px;
}
aside.aside-right section.section-right {
      left: 1%;
}
}
@media only screen and (max-width: 900px) {
.content {
    padding-left: 320px;
}
aside.aside-left {
    width: 320px;
}
}
@media only screen and (max-width: 750px) {
.content {
    padding-left: 0;
}
aside.aside-left {
    width: 100%;
    min-height: auto;
    position: relative;
    margin: 0 auto 10px;
}
aside.aside-right section {
    width: 100%;
    position: relative;
}
aside.aside-right section.section-left {
      left: 0%;
      margin: 0 auto 10px;
}
aside.aside-right section.section-right {
      left: 0%;
      margin: 0 auto;
}
.baseInfo ul {
    display: inline-block;
    width: 48%;
    vertical-align: top;
}
}
@media only screen and (max-width: 580px) {
aside.aside-left {
    width: 100%;
}
aside.aside-left .baseInfo li {
      padding: 10px 10px;
}
aside.aside-left .banner-bg .myInfo {
      padding: 0 10px;
}
aside.aside-right section {
    width: 100%;
    padding: 20px 10px 20px 20px;
}
.baseInfo ul {
    display: block;
    width: 100%;
    vertical-align: top;
}
}
@media print {
a {
    text-decoration: none;
}
.content {
    padding-left: 0;
}
aside.aside-left {
    width: 100%;
    min-height: auto;
    position: relative;
    margin: 0 auto 10px;
}
aside.aside-right section {
    width: 100%;
    position: relative;
}
aside.aside-right section.section-left {
      left: 0%;
      margin: 0 auto 10px;
}
aside.aside-right section.section-right {
      left: 0%;
      margin: 0 auto;
}
.baseInfo ul {
    display: inline-block;
    width: 48%;
    vertical-align: top;
}
}



